import React, { useEffect, useRef, useState } from 'react'
import {
	createWorkRequest,
	updateWorkRequest,
	deleteWorkRequest,
} from '../api/myRequestsApi'
import './RequestModal.css'

//  
interface RequestModalProps {
	//  undefined,    ,
	//     (pendingFromMe).
	requestId?: number
	documentNumber: string
	currentRequestType?: string
	currentProposedDate?: string
	currentNote?: string
	currentReceiver?: string

	//   ,    "" ( / )
	executorName: string
	controllerName: string
	approverName: string

	// 
	onClose: () => void
	onRequestSaved: () => void
}

const RequestModal: React.FC<RequestModalProps> = ({
	requestId,
	documentNumber,
	currentRequestType,
	currentProposedDate,
	currentNote,
	currentReceiver,
	executorName,
	controllerName,
	approverName,
	onClose,
	onRequestSaved,
}) => {
	//  
	const [requestType, setRequestType] = useState<string>('1')
	//  
	const [proposedDate, setProposedDate] = useState<string>('')
	// 
	const [receiver, setReceiver] = useState<string>('')
	// 
	const [note, setNote] = useState<string>('')

	//    
	const [isClosing, setIsClosing] = useState(false)

	//   
	const firstInputRef = useRef<HTMLSelectElement>(null)

	//  / 
	useEffect(() => {
		if (requestId) {
			//  
			setRequestType(currentRequestType || '1')
			setProposedDate(currentProposedDate || getTodayStr())
			setNote(currentNote || '')
			setReceiver(currentReceiver || approverName)
		} else {
			//  
			setRequestType('1')
			setProposedDate(getTodayStr())
			setNote('')
			setReceiver(approverName) //    ""
		}
	}, [
		requestId,
		currentRequestType,
		currentProposedDate,
		currentNote,
		currentReceiver,
		approverName,
	])

	//    
	useEffect(() => {
		if (firstInputRef.current) {
			firstInputRef.current.focus()
		}
	}, [])

	//       ESC
	useEffect(() => {
		const handleEsc = (e: KeyboardEvent) => {
			if (e.key === 'Escape') {
				triggerClose()
			}
		}
		window.addEventListener('keydown', handleEsc)
		return () => {
			window.removeEventListener('keydown', handleEsc)
		}
	}, [])

	// :   'yyyy-MM-dd'
	const getTodayStr = () => {
		const d = new Date()
		const yyyy = d.getFullYear()
		const mm = String(d.getMonth() + 1).padStart(2, '0')
		const dd = String(d.getDate()).padStart(2, '0')
		return `${yyyy}-${mm}-${dd}`
	}

	//    (fade-out)
	const triggerClose = () => {
		setIsClosing(true)
	}

	//     -  onClose
	useEffect(() => {
		if (!isClosing) return

		const timer = setTimeout(() => {
			onClose()
		}, 250) //   fade-out (. CSS)

		return () => clearTimeout(timer)
	}, [isClosing, onClose])

	// 
	const handleCreate = async () => {
		try {
			const resp = await createWorkRequest({
				documentNumber,
				requestType,
				proposedDate,
				note,
				receiver,
			})
			if (!resp.success) {
				alert('  : ' + resp.message)
				return
			}
			alert(' ! requestId=' + resp.requestId)
			onRequestSaved()
			triggerClose()
		} catch (err: any) {
			console.error(err)
			alert('    ')
		}
	}

	// 
	const handleUpdate = async () => {
		if (!requestId) return
		try {
			const resp = await updateWorkRequest({
				id: requestId,
				documentNumber,
				requestType,
				proposedDate,
				receiver,
				note,
			})
			if (!resp.success) {
				alert('  : ' + resp.message)
				return
			}
			alert(' !')
			onRequestSaved()
			triggerClose()
		} catch (ex: any) {
			console.error(ex)
			alert('    ')
		}
	}

	// 
	const handleDelete = async () => {
		if (!requestId) return
		const conf = window.confirm(' ?')
		if (!conf) return
		try {
			const resp = await deleteWorkRequest({
				requestId,
				documentNumber,
			})
			if (!resp.success) {
				alert('  : ' + resp.message)
				return
			}
			alert(' !')
			onRequestSaved()
			triggerClose()
		} catch (ex: any) {
			console.error(ex)
			alert('    ')
		}
	}

	return (
		/* 
		    "modal-dialog-centered",     
		     ;   
		       .
		*/
		<div
			className={`modal-backdrop-custom ${
				isClosing ? 'modal-closing' : 'modal-opening'
			}`}
		>
			<div className='modal-dialog-custom'>
				<div className='modal-content-custom'>
					<div className='modal-header-custom'>
						<h5 className='modal-title-custom'></h5>
						<button
							type='button'
							className='btn-close-custom'
							onClick={triggerClose}
							aria-label=''
						>
							&times;
						</button>
					</div>
					<div className='modal-body-custom'>
						{/*   */}
						<div className='mb-3'>
							<label htmlFor='requestTypeSelect' className='form-label'>
								 
							</label>
							<select
								id='requestTypeSelect'
								className='form-select'
								value={requestType}
								onChange={e => setRequestType(e.target.value)}
								ref={firstInputRef} // <-- 
							>
								<option value='1'>1</option>
								<option value='2'>2</option>
								<option value='3'>3</option>
								<option value=''> </option>
							</select>
						</div>

						{/*   */}
						<div className='mb-3'>
							<label htmlFor='proposedDate' className='form-label'>
								 
							</label>
							<input
								type='date'
								id='proposedDate'
								className='form-control'
								value={proposedDate}
								onChange={e => setProposedDate(e.target.value)}
							/>
						</div>

						{/*  ( / ) */}
						<div className='mb-3'>
							<label htmlFor='receiverSelect' className='form-label'>
								
							</label>
							<select
								id='receiverSelect'
								className='form-select'
								value={receiver}
								onChange={e => setReceiver(e.target.value)}
							>
								<option value={controllerName}>
									 ({controllerName})
								</option>
								<option value={approverName}>
									 ({approverName})
								</option>
							</select>
						</div>

						{/*  */}
						<div className='mb-3'>
							<label htmlFor='requestNote' className='form-label'>
								
							</label>
							<textarea
								id='requestNote'
								className='form-control'
								rows={3}
								value={note}
								onChange={e => setNote(e.target.value)}
							/>
						</div>
					</div>

					<div className='modal-footer-custom'>
						{/*  requestId  ->  .
						      requestId ->  / */}
						{!requestId && (
							<button
								type='button'
								className='btn btn-primary'
								onClick={handleCreate}
							>
								
							</button>
						)}
						{requestId && (
							<>
								<button
									type='button'
									className='btn btn-success'
									onClick={handleUpdate}
								>
									
								</button>
								<button
									type='button'
									className='btn btn-danger'
									onClick={handleDelete}
								>
									
								</button>
							</>
						)}
						<button
							type='button'
							className='btn btn-secondary'
							onClick={triggerClose}
						>
							
						</button>
					</div>
				</div>
			</div>
		</div>
	)
}

export default RequestModal
