import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import {
	getMyRequests,
	setRequestStatus,
	MyRequestDto,
} from '../../api/myRequestsApi'

import './MyRequestsPage.css' //    

//       "2025-03-12T00:00:00"  "12.03.2025"
function formatDate(dateStr?: string): string {
	if (!dateStr) return ''
	const date = new Date(dateStr)
	if (isNaN(date.getTime())) return dateStr //   ,   
	const day = String(date.getDate()).padStart(2, '0')
	const month = String(date.getMonth() + 1).padStart(2, '0')
	const year = date.getFullYear()
	return `${day}.${month}.${year}`
}

const MyRequestsPage: React.FC = () => {
	const navigate = useNavigate()

	//        
	const [requests, setRequests] = useState<MyRequestDto[]>([])
	const [error, setError] = useState<string>('')

	//        
	useEffect(() => {
		const token = localStorage.getItem('jwtToken')
		if (!token) {
			navigate('/login')
			return
		}
		getMyRequests()
			.then(data => setRequests(data))
			.catch(err => {
				console.error(err)
				setError(err.response?.data || '   ')
			})
	}, [navigate])

	//      (/)
	const handleSetStatus = async (
		req: MyRequestDto,
		newStatus: 'Accepted' | 'Declined'
	) => {
		if (
			!window.confirm(
				`   ${
					newStatus === 'Accepted' ? '' : ''
				} ?`
			)
		) {
			return
		}
		try {
			const resp = await setRequestStatus(
				req.id,
				req.workDocumentNumber,
				newStatus
			)
			if (resp.success) {
				alert(' ')
				const updated = await getMyRequests()
				setRequests(updated)
			} else {
				alert(`: ${resp.message || ''}`)
			}
		} catch (ex: any) {
			console.error(ex)
			alert('   ')
		}
	}

	//   ,  
	if (error) {
		return (
			<div className='container-fluid mt-3 fade-in my-requests-page'>
				<h4 className='mb-3'>  </h4>
				<div className='alert alert-danger'>{error}</div>
			</div>
		)
	}

	return (
		//  container-fluid    
		<div className='container-fluid mt-3 fade-in my-requests-page'>
			<div className='d-flex justify-content-between mb-3'>
				<h4 className='section-title'>  </h4>
				<button className='btn btn-secondary' onClick={() => navigate('/')}>
					 
				</button>
			</div>

			{requests.length > 0 ? (
				<div
					className='table-responsive'
					style={{ maxHeight: '70vh', overflowY: 'auto' }}
				>
					<table className='table table-bordered table-hover'>
						<thead>
							<tr className='sticky-header'>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th></th>
								<th>1</th>
								<th>2</th>
								<th>3</th>
								<th> </th>
								<th> </th>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							{requests.map(r => {
								//        
								const rowClass =
									r.requestType === '' ? 'table-info' : 'table-warning'

								return (
									<tr key={r.id} className={rowClass}>
										<td>{r.documentName}</td>
										<td>{r.workName}</td>
										<td>{r.executor}</td>
										<td>{r.controller}</td>
										<td>{r.receiver}</td>
										{/*       formatDate */}
										<td>{formatDate(r.planDate)}</td>
										<td>{formatDate(r.korrect1)}</td>
										<td>{formatDate(r.korrect2)}</td>
										<td>{formatDate(r.korrect3)}</td>
										<td>{r.requestType}</td>
										<td>{formatDate(r.proposedDate)}</td>
										<td>{r.sender}</td>
										<td>{r.note}</td>
										<td>
											<button
												className='btn btn-sm btn-approve me-2'
												onClick={() => handleSetStatus(r, 'Accepted')}
											>
												
											</button>
											<button
												className='btn btn-sm btn-decline'
												onClick={() => handleSetStatus(r, 'Declined')}
											>
												
											</button>
										</td>
									</tr>
								)
							})}
						</tbody>
					</table>
				</div>
			) : (
				<div className='alert alert-info'>  .</div>
			)}
		</div>
	)
}

export default MyRequestsPage
